/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/


/*---------------------------- Buttons and Fields ---------------------------- */

input[type=submit] {
    background-color: #222;
    border: 2px solid #0c0;
    border-radius: 3px;
    color: #0c0;
    font-size: 1rem;
    font-weight: 500;
    line-height: 1;
	padding: .75rem 2.25rem;
    margin-bottom: 1.5rem;

    @include tablet {
        font-size: 1.25rem;
   }
}

button, 
input[type=button], 
input[type=reset], 
input[type=text],
textarea[type=text] {
    background-color: #222;
    border: 2px solid #0c0;
    border-radius: 3px;
    color: #fff;
    font-size: 1rem;
    font-weight: 500;
    line-height: 1;
	padding: .75rem 2.25rem;
    margin-bottom: 1.5rem;
    
    &::selection {
        color: #0c0;
      }

    @include tablet {
         font-size: 1.25rem;
    }
}

input[type=search] {
    background-color: #222;
    border: 2px solid #0c0;
    border-radius: 3px;
    color: #fff;
    font-size: 1rem;
    margin-bottom: 1.5rem;
    padding: .25rem .5rem;
}

input[type=email]:focus, 
input[type=password]:focus, 
input[type=search]:focus, 
input[type=text]:focus, 
input[type=url]:focus, 
textarea:focus {
    border-color: #fcfcd2;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password] {
    padding: 3px;

    @include tablet {
        width: 65%;
    }
}

.search-form input[type=search] {
    width: 240px;
}

textarea {
	padding-left: 3px;
	width: 100%;
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

label {
    display: block;
    margin-bottom: .75rem;
}

/*---------------------------- New Quote Button ---------------------------- */

#new-quote-button {
    display: block;
		&:focus{
			outline: none;
	}
}	

button.new-quote-button {
    margin-top: $base__space-unit;
    color: #0c0;
    

	@include desktop {
        margin: 3rem auto 0 auto;
        display: block;
    }	
}
